<template>
  <el-container style="height: 100%;">
    <el-aside
      :width="aside_witdh"
      style="
        height: 100vh;
        width:auto;
        background-color: rgb(238, 241, 246);
        margin-left: -1px;
      "
    >
      <Aside :isCollapse="isCollapse" :style="{width: aside_witdh}"></Aside>
    </el-aside>
    <el-container style="position: relative;height: 100vh;left: 0">
      <el-header
      :style="{ position: 'fixed',
      top: 0, 
      bottom: '8%', 
      left: aside_witdh, 
      right: 0 }"
      style="text-align: 'right';
      border-bottom: rgba(168,168,168,0.3) 1px solid;
      background-color: rgba(255,255,255,1);
      font-size: 15;"
      >
      
        <Header @doCollapse="doCollapse" :icon="icon"></Header>
      </el-header>
      <el-main :style="{ position: 'fixed', top: '10%', bottom: 0, left: aside_witdh, right: 0 }">
        <router-view />
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import Aside from './Aside'
import Header from './Header'
import {ref} from 'vue'
export default {
  name: 'I-ndex',
  components: { Aside, Header },
  setup() {
    const isCollapse = ref(false)
    const icon = ref('Fold')
    const aside_witdh = ref('15vw')
    function doCollapse() {
      isCollapse.value = !isCollapse.value
      if (!isCollapse.value) {
        // 展开
        aside_witdh.value = '15vw'
        icon.value = 'Fold'
      } else {
        //关起、关闭、收起
        aside_witdh.value = '5vw'
        icon.value = 'Expand'
      }
    }
    return {
      aside_witdh,
      icon,
      doCollapse,
      isCollapse,
    }
  },
}
</script>

<style>
.el-header{
  padding: 1px;
}

.el-aside {
  color: #333;
  bottom: 0;
}
.el-main {
  padding: 0;
}
</style>
